<template>
  <div class="mapContainer" id="map"></div>
</template>

<script>
import {
  map,
  tileLayer,
  layerGroup,
  markerClusterGroup,
  marker,
  canvas,
  Icon,
  divIcon,
  latLng,
  CRS
} from "leaflet";
import { layerProviders } from "../js/layerProvider.js"; //地图基础图层
import { EventBus } from "../js/eventBus.js"; //事件载体
import "../css/style.css"; //全局样式
import "leaflet/dist/leaflet.css";
export default {
  name: "MyMap",
  data() {
    return {
      thisMap: new Object(), //全局二维地图
      markerImgUrl: require("../assets/marker-icon.png")
    };
  },
  methods: {
    initMap: function() {
      var that = this;
      var thisMap = map(this.$el, {
        center: [37.512452, 122.123646],
        zoom: 13,
        zoomControl: false,
        renderer: canvas(),
        attributionControl: false,
        closePopupOnClick: false
      });
      this.thisMap = thisMap; //初始化data中的thismap
      var tiandituNormalMap = tileLayer(layerProviders.TianDiTu.Normal.Map, {
        subdomains: layerProviders.TianDiTu.subdomains,
        key: layerProviders.TianDiTu.key,
        id: "tiandituNormalMap"
      });
      var tiandituNormalAnnotion = tileLayer(
        layerProviders.TianDiTu.Normal.Annotion,
        {
          subdomains: layerProviders.TianDiTu.subdomains,
          key: layerProviders.TianDiTu.key,
          id: "tiandituNormalAnnotion"
        }
      );

      layerGroup([tiandituNormalMap, tiandituNormalAnnotion]).addTo(thisMap);
    },
    initEvents: function() {
      var that = this;
      EventBus.$on("simpleMap", function(poiName, lat, lng) {
        var Icon = divIcon({
          html:
            `
                    <div class="content">` +
            poiName +
            `
                    </div>
                    <div class="triangle"></div>
                    <img class="marker" src="` +
            that.markerImgUrl +
            `">
                `,
          iconSize: [100, 100],
          className: "class-Element"
        });

        var elementMarker = marker(
          latLng({
            lng: lng,
            lat: lat
          }),
          {
            icon: Icon
          }
        ).addTo(that.thisMap);

        that.thisMap.setView(
          latLng({
            lng: lng,
            lat: lat
          }),
          17
        );
      });
    }
  },
  mounted: function() {
    this.initMap();
    this.initEvents();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.mapContainer {
  height: 100%;
  width: 100%;
}
</style>



