<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
import { EventBus } from "./js/eventBus.js";
export default {
  name: "App",
  methods: {
    simpleMap(poiName, lat, lng) {
      EventBus.$emit("simpleMap", poiName, lat, lng);
    }
  },
  mounted() {
    window.simpleMap = this.simpleMap;
  }
};
</script>

<style>
html,
body,
#app {
  height: 100%;
  width: 100%;
  margin: 0;
  pointer-events: none;
}
</style>



